import React from 'react'
import './Acerca.css'
export default function AcercaPublic() {
  return (
    <div className='container-acerca'id='Acerca'>
      <h1>Sobre Nosotros</h1>
        <div className='sub-acerca'>
            <div className='acerca-texto'>
                <h2>Antecentes</h2>
                <p>
                El centro de bachillerato tecnolgico agropecuario No. 5 inicia sus actividades el 02 de octubre de 1972 en la ciudad de Huejutla de Reyes Hidalgo; se encuentra localizado en al norte de la ciudad, sobre el Boulevard Adolfo Lpez Mateos s/n colonia aviacin civil. Limitando al norte con el estado de Veracruz a travs del Ro los Cantores y la Colonia Silvano Cruz, al sur con la colonia Adolfo Lpez Mateos, al oriente con el Boulevard Adolfo Lpez Mateos y al poniente con el Ro los Cantores.
                </p>
                <p>
                El Centro de Bachillerato Tecnolgico Agropecuario No.5 desde su fundacin inicio ofertando dos especialidades como centro de estudios tecnolgico agropecuario, la de tcnico agrcola y pecuario, en el ao de 1976 se incorpora una nueva especialidades industrias agropecuarias, en 1980 se incorpora las especialidades de tcnico caero, tcnico agropecuario especialista en plagas y enfermedades, en horticultura y porcino cultura, industrializacin y productos lcteos, en el ao de 1984 se inicia el bachillerato incorporado la especialidad de tcnico agropecuario, en el ao de 1987 inicia la extensin en Huautla, Hgo. , liquidndose en el ao de 1990.
                </p>
                <p>
                En el ao de 1991 se incorporan las especialidades de desarrollo comunitario y tcnico en informtica agropecuaria, en el ao 2000 empieza el sistema automatizado de control escolar que viene a simplificar la captura de datos en el departamento de servicios escolares, en el ao 2002 se incorpora la especialidad de administracin y contabilidad rural. Y en agosto de 2004 se inicia con el nuevo modelo educativo de Bachilleratos Tecnolgicos.
                </p>
            </div>
        </div>
    </div>
  )
}
